    newCooperatorTableValueList: any = [];//
    windowData: any;//
    newCooperatorDispaly: boolean = false;//
    newProportionDispaly: boolean = false;//
    newDirectorDispaly: boolean = false;//
    newDirectorLisr: any = [];//
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;
    UPLOAD: string = TZB_CUS_UPLOAD;
    newBusinessTypeList: any = [];//
    // 
    newUserTableValueList: any = [];//
    orgTreeDisplay: boolean = false;
    ownerOrgInValue: any;
