    // 
    toQH(i) {

        if (i == 1) {
            document.getElementById("user").className = 'titBgColor active';
            document.getElementById("cooperator").className = 'titBgColor';
            document.getElementById("oldAssistantInformation").className = 'titBgColor';
            this.userDisplay = true;
            this.preReserveSupervisorDisplay = false;
            this.cooperatorDisplay = false;
            this.queryUser();
        }
        if (i == 2) {
            document.getElementById("user").className = 'titBgColor';
            document.getElementById("cooperator").className = 'titBgColor active';
            document.getElementById("oldAssistantInformation").className = 'titBgColor';
            this.preReserveSupervisorDisplay = false;
            this.cooperatorDisplay = true;
            this.userDisplay = false;
            this.queryCooperator();
        }
        if (i == 3) {
            document.getElementById("user").className = 'titBgColor';
            document.getElementById("cooperator").className = 'titBgColor';
            document.getElementById("oldAssistantInformation").className = 'titBgColor active';
            this.preReserveSupervisorDisplay = true;
            this.cooperatorDisplay = false;
            this.userDisplay = false;
            this.batchDisplay = false;
            this.preReserveSupervisor();
        }
    }

