    // 
    queryUserDetail(item) {
        let param = {
            busType: this.queryBean.newBusinessType,//
            accountNo: this.queryBean.newUserNo,//
        };
        this.httpService.queryAccountInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.queryBean.newCustomerNo = data.custCode;//
                this.queryBean.newCustomerName = data.custName;//
                this.queryBean.newOpenInstitution = data.openBanch;//
                this.queryBean.newOpenInstitutionName = data.openBanchName;//
                this.queryBean.newOldInto = data.oldDepInfo;//
                this.queryBean.newSubjectNo = data.kmh;//
                this.queryBean.newBankCode = data.bankCode;//
                if (item == 'save') {
                    setTimeout(() => {
                        this.add();
                    }, 1000)
                }
            } else {
                this.emptyAccount();
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.emptyAccount();
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        });
    }

    // 
    emptyAccount() {
        this.queryBean.newCustomerNo = undefined;//
        this.queryBean.newCustomerName = undefined;//
        this.queryBean.newOpenInstitution = undefined;//
        this.queryBean.newOpenInstitutionName = undefined;//
        this.queryBean.newOldInto = undefined;//
    }

    // -
    newPreservation() {
        // 
        if (this.queryBean.newBusinessType == null || this.queryBean.newBusinessType == undefined || this.queryBean.newBusinessType == "") {
            this.newBusinessTypeDispaly = true;
        } else {
            this.newBusinessTypeDispaly = false;
        }
        if (this.queryBean.newUserNo == null || this.queryBean.newUserNo == undefined || this.queryBean.newUserNo == "") {
            this.newUsernameDispaly = true;
        } else {
            this.newUsernameDispaly = false;
        }
        if (this.newBusinessTypeDispaly || this.newUsernameDispaly) {
            return
        }
        // 
        if (this.queryBean.newCustomerNo == null || this.queryBean.newCustomerNo == undefined || this.queryBean.newCustomerNo == "" ||
            this.queryBean.newOpenInstitutionName == null || this.queryBean.newOpenInstitutionName == undefined || this.queryBean.newOpenInstitutionName == ""
        ) {
            this.queryUserDetail('save');// 
        } else {
            // 
            this.add();
        }
    }

    // -
    add() {
        // 
        if (this.queryBean.newCustomerNo == null || this.queryBean.newCustomerNo == undefined || this.queryBean.newCustomerNo == "" ||
            this.queryBean.newOpenInstitutionName == null || this.queryBean.newOpenInstitutionName == undefined || this.queryBean.newOpenInstitutionName == "") {
            this.msgs = [];
            this.msgs = [{ severity: 'erro', summary: '', detail: '' }];
            return
        }
        // 
        let bool = true;
        for (var i = 0; i < this.newUserTableValueList.length; i++) {
            if (this.newUserTableValueList[i].accountNo == this.queryBean.newUserNo) {
                bool = false;
                break;
            }
        }
        // 
        if (bool) {
            let param = {
                busType: this.queryBean.newBusinessType,//
                seqno: this.windowShareholdingNo,//
                accountNo: this.queryBean.newUserNo,//
                custCode: this.queryBean.newCustomerNo//
            };
            this.httpService.checkAccountInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.newUserTableValueListAdd = [];
                    this.newUserTableValueListAdd.push({
                        "busType": this.queryBean.newBusinessType,
                        "accountNo": this.queryBean.newUserNo,
                        "custCode": this.queryBean.newCustomerNo,
                        "custName": this.queryBean.newCustomerName,
                        "openBranch": this.queryBean.newOpenInstitution,
                        "openBanchName": this.queryBean.newOpenInstitutionName,
                        "oldDepInfo": this.queryBean.newOldInto,
                        "kmh": this.queryBean.newSubjectNo,
                        "bankCode": this.queryBean.newBankCode
                    });
                    this.newUserBtn();
                    // this.newUserReset();
                } else {
                    this.emptyAccount()
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                }
            }, err => {
                this.emptyAccount()
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            });
        } else {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: "" }];
            this.newUserReset();
        }
    }

