    createJointStocksInfoByPresup() {
        let param = {
            operationType: 0,//
            seqno: this.shareholdingNo,//
            accountList: this.tableValueListAdd//List
        };
        this.httpService.createJointStocksInfoByCust(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                // 
                this.userResetPage();
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        });
    }

    // 
    userCustAccountQuery() {// Step4(tableValueListAdd)

        if (this.tableValueListAdd.length == 0) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            this.isCustAdd = false;
            this.diviType = '';
        } else {
            this.userResetAllInfo();
            let param = {
                operationType: 0,//
                seqno: this.shareholdingNo,//
                accountList: this.tableValueListAdd//List
            };
            this.httpService.createJointStocksInfoByCust(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    // 
                    this.userResetPage();
                } else {
                    this.isCustAdd = false;
                    this.diviType = '';
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                }
            }, err => {
                this.isCustAdd = false;
                this.diviType = '';
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            });
        }
    }

    // 
    showTime(param1, param2) {
        if (param1 == 'plan') {
            if (this.startDate && this.divideLoseDate) {
                if (this.startDate > this.divideLoseDate) {
                    this.msgs = [];
                    this.msgs.push({ severity: 'error', summary: '', detail: '' });
                    setTimeout(() => {
                        if (param2 == "start") {
                            this.startDate = null;
                        } else {
                            this.divideLoseDate = null;
                        }
                    })
                }
            }
        }
    }

