    //   --2018_05_29
    queryUserPermissionXD(custList: any, userLoginId: any) {
        let TZB_HTTP_CUS1 = this.TZB_HTTP_CUS + API.selectPartyRelationByAdminDoor;
        let TZB_HTTP_CUS_lmy = this.TZB_HTTP_CUS_lmy + API.queryUserPermissionSourcesByList;
        let params = this.handleParams({ 'custList': custList })
        let _self = this;
        $.ajax({
            url: TZB_HTTP_CUS1,
            type: 'POST',
            data: JSON.stringify(params),
            async: false,
            headers: {
                'x-ajax': '1',
                'Content-Type': 'application/json'
            },
            dataType: 'json',
            success: function (data) {
                let asociationList = data.custList;
                // let param = _self.handleParams({ 'userLoginId': userLoginId, 'sourceId': '', 'custNoAndRelationGroupList': asociationList });
                let param = {
                    userLoginId: userLoginId,
                    permissionId: _self.permissionId,
                    custNoAndRelationGroupList: asociationList
                }
                // 2019.03.04 15:26
                param = _self.handleParams(param);
                $.ajax({
                    url: TZB_HTTP_CUS_lmy,
                    type: 'POST',
                    data: JSON.stringify(param),
                    async: false,
                    headers: {
                        'x-ajax': '1',
                        'Content-Type': 'application/json'
                    },
                    dataType: 'json',
                    success: function (datas) {
                        if (datas.returnCode.code == 'success') {
                            if (datas.resultList[0].roleType) {
                                let commonHeader = JSON.parse(_self.commfunc.getSessionData('commonHeader'));
                                commonHeader['extParam']['roleType'] = datas.resultList[0].roleType;
                                _self.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                            }
                            _self.sourceIdList = datas.resultList[0].dataSourceList;
                            _self.commfunc.setSessionDataCH('filedId', JSON.stringify(_self.sourceIdList));
                        }
                    },
                    error: function (err) {
                        console.log(err);
                    }
                })
            },
            error: function (error) {
                console.log(error);
            }
        })
    }

    // ajax
    handleParams(params) {
        let session = window.sessionStorage;
        let commonHeader: Object = {};
        if (session.getItem('para')) {
            this.userMesg1 = JSON.parse(atob(session.getItem('para')));
            if (this.userMesg1['taskCategoryId'] || this.userMesg1['scheduleId']) {
                commonHeader['taskCategoryId'] = this.userMesg1['taskCategoryId'];
                commonHeader['scheduleId'] = this.userMesg1['scheduleId'];
            }
        }
        this.userMesg = JSON.parse(atob(session.getItem('commonHeader')));
        console.log(this.userMesg)
        //
        commonHeader['comSERVICE_CODE'] = params['comSERVICE_CODE'];
        commonHeader['comMESSAGE_CODE'] = params['comMESSAGE_CODE'];
        if (params && (params['taskCategoryId'] || params['scheduleId'])) {
            commonHeader['taskCategoryId'] = params['taskCategoryId'];
            commonHeader['scheduleId'] = params['scheduleId'];
        }
        if (this.userMesg['extParam']) {
            commonHeader['extParam'] = this.userMesg['extParam']
        }
        commonHeader['tranDate'] = moment().format('YYYYMMDD');
        commonHeader['tranTime'] = moment().format('HHmmssSSS');
        commonHeader['lastTimeZone'] = this.userMesg['lastTimeZone'];
        commonHeader['userId'] = this.userMesg['userId'];
        commonHeader['orgId'] = this.userMesg['orgId'];
        commonHeader['userRoles'] = this.userMesg['userRoles'];
        commonHeader['targetId'] = this.userMesg['targetId'];//
        commonHeader['globalSeqNo'] = '';//
        if (this.userMesg['scheduleId'] || this.userMesg['taskCategoryId']) {
            commonHeader['taskCategoryId'] = this.userMesg['taskCategoryId'];
            commonHeader['scheduleId'] = this.userMesg['scheduleId'];
        }
        params['commonHeader'] = commonHeader;
        return params;
    }

