    // 
    queryUserPermission(custList: any, userLoginId: any) {
        let TZB_HTTP_CUS1 = this.TZB_HTTP_CUS + API.selectPartyRelationByAdminDoor;
        let params = this.handleParams({ 'custList': custList })
        let _self = this;
        $.ajax({
            url: TZB_HTTP_CUS1,
            type: 'POST',
            data: JSON.stringify(params),
            async: false,
            headers: {
                'x-ajax': '1',
                'Content-Type': 'application/json'
            },
            dataType: 'json',
            success: function (data) {
                let asociationList = data.custList;
                //
                let TZB_HTTP_CUS_A = _self.TZB_HTTP_CUS + API.selectPartyRelationshipType;
                let para = _self.commfunc.handleParams({ parentTypeId: '0303', queryType: '2' });
                $.ajax({
                    url: TZB_HTTP_CUS_A,
                    type: 'POST',
                    data: JSON.stringify(para),
                    async: false,
                    headers: {
                        'x-ajax': '1',
                        'Content-Type': 'application/json'
                    },
                    dataType: 'json',
                    success: function (datas) {
                        let array1 = [];
                        let array2 = [];
                        for (let i = 0; i < datas.partyRelationshipTypeList.length; i++) {
                            array1[datas.partyRelationshipTypeList[i].relationshipType] = true;
                        }
                        for (var i = 0; i < asociationList[0].custInfoList.length; i++) {
                            if (!array1[asociationList[0].custInfoList[i].custManagerNos]) {
                                array2.push(asociationList[0].custInfoList[i])
                            }
                        }
                        asociationList[0].custInfoList = array2;
                        let params = _self.handleParams({ 'userLoginId': userLoginId, 'sourceId': '', 'custNoAndRelationGroupList': asociationList })
                        console.log(params)
                        _self.queryUserPermissionSourcesByList(params);
                    }
                })

            },
            error: function (error) {
                console.log(error);
            }
        })
    }

    //  2018-06-08-
    queryUserPermissionSourcesByList(param) {
        let params = this.commfunc.handleParams(param)
        console.log(params)
        let TZB_HTTP_CUS_lmy = this.TZB_HTTP_CUS_lmy + API.queryUserPermissionSourcesByList;
        let _self = this;
        $.ajax({
            url: TZB_HTTP_CUS_lmy,
            type: 'POST',
            data: JSON.stringify(params),
            async: false,
            headers: {
                'x-ajax': '1',
                'Content-Type': 'application/json'
            },
            dataType: 'json',
            success: function (datata) {
                if (datata.resultList[0].roleType) {
                    let commonHeader = JSON.parse(_self.commfunc.getSessionData('commonHeader'));
                    commonHeader['extParam']['roleType'] = datata.resultList[0].roleType;
                    _self.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
                }
                if (datata.resultList && datata.resultList.length > 0) {
                    _self.sourceIdList = datata.resultList[0].dataSourceList;
                } else {
                    _self.sourceIdList = {};
                }
                _self.commfunc.setSessionDataCH('filedId', JSON.stringify(_self.sourceIdList));
            },
            error: function (err) {
                console.log(err);
            }
        })
    }

