import { Component, OnInit, Output, Input } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ConfirmationService, Message } from 'primeng/primeng';
import { CusViewHttpService } from 'app/pages/tzb/custom/http/custom-view/custom-view.http.service';
import { Commfunc } from '../../../../../../../@uisftech/common/service/commonFun';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from '../../../../../../../@uisftech/common/service/http.constant';
import { stockSystemMarketingUnauditedQueryBean } from 'app/pages/tzb/custom/work-rask/credit-intergration/stockSystemMarketing/stockSystemMarketingUnauditedQuery/bean/stockSystemMarketingUnauditedQueryBean';
import { stockSystemMarketingCooperatorBean } from 'app/pages/tzb/custom/work-rask/credit-intergration/stockSystemMarketing/stockSystemMarketingApply/bean/stockSystemMarketingCooperatorBean';
import { stockSystemMarketingBean } from 'app/pages/tzb/custom/work-rask/credit-intergration/stockSystemMarketing/stockSystemMarketingApply/bean/stockSystemMarketingBean';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import * as moment from 'moment';

@Component({
    selector: 'stockSystemMarketingExam',
    templateUrl: './stockSystemMarketingExam.component.html',
    styleUrls: ['./stockSystemMarketingExam.component.scss'],
    providers: [Commfunc, ConfirmationService]
})
export class stockSystemMarketingExamComponent implements OnInit {

    queryBean: stockSystemMarketingUnauditedQueryBean = new stockSystemMarketingUnauditedQueryBean();

    cooperatorBean: stockSystemMarketingCooperatorBean = new stockSystemMarketingCooperatorBean();
    bean: stockSystemMarketingBean = new stockSystemMarketingBean();

    msgs: Message[] = [];
    depositBase: string;//
    loanBase: string;//

    detailList: any = [];
    arrayList: any = [];
    bigCustNo: string;

    userDisplay: boolean = false;
    userTableValueList: any = [];
    userFirst: number = 0;
    userPageNum: number = 1;
    userPageSize: number = 10;
    userTotal: string;

    cooperatorDisplay: boolean = false;
    cooperatorTableValueList: any = [];
    cooperatorFirst: number = 0;
    cooperatorPageNum: number = 1;
    cooperatorPageSize: number = 20;
    cooperatorTotal: string;

    creater: string;
    seqno: string;
    createDate: string;
    brid: string;

    stockSeqno: string;

    isWorkCreator: boolean = false;

    businessTypeList: any = [];
    code: any;


    tableValueList: any = [];
    tableValueListAdd: any = [];
    cooperatorTableValueListAdd: any = [];
    uploadedFiles: any[] = [];
    succssNum: any = 0;
    failNum: any = 0;
    failInfoList: any = [{ 'rowNum': '', 'failReason': '' }];
    directorLisr: any = [];
    cooperatorDispaly: boolean = false;
    proportionDispaly: boolean = false;
    directorDispaly: boolean = false;
    businessTypeDispaly: boolean = false;
    usernameDispaly: boolean = false;
    // 
    shareholdingNo: string;
    founder: string;
    foundDate: string;
    institution: string;
    stockInfo: any;
    taskRate: string;
    canUp: boolean = false;
    flag: string;
    isHistory: boolean = false;
    theRequest: any = [];
    isShowZG: boolean = false;

    // 
    batchPageFirst: number = 0;
    batchPageNum: number = 1;//
    batchPageSize: number = 10;
    batchTotal: string;//
    tableValueBatchList: any = [];
    errorPage: boolean = false;//
    sumNum: number = 0;
    goflag: string;
    DOWNLOAD: string = TZB_CUS_DOWNLOAD;
    UPLOAD: string = TZB_CUS_UPLOAD;
    batchDisplay: boolean = false;
    custType: string;
    userAllCard: boolean = false;
    noUpdate: boolean = false;
    busType: string;
    divideLoseDate: Date = null;
    ch: any = CALENDAR_CN;
    isLoseEfficacyAssit: any = []; // 

    // 
    divideStartTime: Date = null;
    divideEndTime: Date = null;
    isLoseEfficacyAssitDispaly: boolean = false;
    preReserveSupervisorDisplay: boolean = false;
    preReserveSupervisorList: any = [];
    seqType: string;
    custDivide: boolean = false;

    constructor(
        public routeInfo: ActivatedRoute,
        public httpService: CusViewHttpService,
        public commfunc: Commfunc,
        public confirmationService: ConfirmationService
    ) {

        let url = location.href;  //url
        if (url.indexOf(";") != -1) {
            let str = url.substr(1);
            let strs = str.split(";");
            for (let i = 0; i < strs.length; i++) {
                this.theRequest[strs[i].split("=")[0]] = (strs[i].split("=")[1]);
            }
        }
        this.founder = decodeURI(this.theRequest.workCreateName)
        this.codeValues();
        this.businessTypeList = this.code['BusinessTypeList'];
        this.isLoseEfficacyAssit = this.code['IsLoseEfficacyAssit'];
        this.isLoseEfficacyAssit = [
            { label: '', value: '' },
            { label: '', value: '0' },
            { label: '', value: '1' }
        ];
        this.detailList = this.routeInfo.snapshot.params;
        this.depositBase = this.arrayList.depositBase;
        this.loanBase = this.arrayList.loanBase;
        this.bigCustNo = this.arrayList.bigCustNo;
        this.arrayList = JSON.parse(this.routeInfo.snapshot.params['dataMap']);
        this.taskRate = this.routeInfo.snapshot.params['taskRate'];
        this.flag = this.routeInfo.snapshot.params['markFlag'];
        if (this.flag == '1') {
            this.isHistory = false;
        } else {
            this.isHistory = true;
        }
        if (this.taskRate == '1' && !this.isHistory) {
            this.canUp = true;
        } else {
            this.canUp = false;
        }
        this.shareholdingNo = this.arrayList.seqno;
        this.seqType = this.arrayList.busType; 
        if (this.canUp) {
            let startTime = moment(new Date()).format('Y-MM-DD');
            let endTime = moment(new Date()).add(3, 'year').format('Y-MM-DD');
            this.divideStartTime = new Date(startTime);
            this.divideEndTime = new Date(endTime);
        }
        if (this.seqType == '04') { // 
            this.custDivide = true;
        } else {
            this.custDivide = false;
        }
    }

    //
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }

    ngOnInit() {
        // 
        this.custType = '1';
        this.userDisplay = true;
        // 
        this.queryStock();
        this.toQH(1);
        this.userResetPage();
        this.preReserveSupervisor();
        this.cooperatorResetPage();
    }

    changeCustType(param) {//
        if (param == '2') {
            if (this.bean.businessType == '' || this.bean.businessType == null || this.bean.businessType == undefined) {
                this.custType = '1';
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: "" }];
                return
            }
            if (this.divideLoseDate == null || this.divideLoseDate == undefined) {
                this.custType = '1';
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: "" }];
                return
            }
        }
        this.userDisplay = false;
        this.preReserveSupervisorDisplay = false;
        this.cooperatorDisplay = false;
        this.batchDisplay = false;
        this.businessTypeDispaly = false;
        this.usernameDispaly = false;
        this.cooperatorDispaly = false;
        this.proportionDispaly = false;
        this.directorDispaly = false;
        this.isLoseEfficacyAssitDispaly = false;
        if (param == '1') {
            this.userDisplay = true;
            this.preReserveSupervisorDisplay = false;
            this.cooperatorDisplay = false;
            this.batchDisplay = false;
        }
        if (param == '2') {
            this.userDisplay = false;
            this.batchDisplay = true;
            this.preReserveSupervisorDisplay = false;
            this.cooperatorDisplay = false;
        }
    }

    toQH(i) {// 
        if (i == 1) {
            document.getElementById("userInformation").className = 'titBgColor active';
            document.getElementById("cooperatorInformation").className = 'titBgColor';
            document.getElementById("oldAssistantInformation").className = 'titBgColor';
            this.userDisplay = true;
            this.preReserveSupervisorDisplay = false;
            this.cooperatorDisplay = false;
            this.batchDisplay = false;
            this.custType = '1';
            this.userFirst = 0;
            this.userPageNum = 1;//
            this.userPageSize = 10;
            this.queryAddUserList();
        }
        if (i == 2) {
            document.getElementById("userInformation").className = 'titBgColor';
            document.getElementById("cooperatorInformation").className = 'titBgColor active';
            document.getElementById("oldAssistantInformation").className = 'titBgColor';
            this.preReserveSupervisorDisplay = false;
            this.cooperatorDisplay = true;
            this.userDisplay = false;
            this.batchDisplay = false;
            this.cooperatorFirst = 0;
            this.cooperatorPageNum = 1;//
            this.cooperatorPageSize = 20;
            this.queryAddCooperatorList();
        }
        if (i == 3) {
            document.getElementById("userInformation").className = 'titBgColor';
            document.getElementById("cooperatorInformation").className = 'titBgColor';
            document.getElementById("oldAssistantInformation").className = 'titBgColor active';
            this.preReserveSupervisorDisplay = true;
            this.cooperatorDisplay = false;
            this.userDisplay = false;
            this.batchDisplay = false;
            if (!this.canUp) {
                this.preReserveSupervisor();
            }
        }
    }

    queryStock() {// -
        let param = {
            operationType: '1',
            seqno: this.shareholdingNo,//
            pageNum: 1,
            pageSize: 10,
        }
        this.httpService.queryJointStocksApp(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if (data.stocksAppList && data.stocksAppList.length >= 0) {

                }
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        });
    }

    // ----------------------
    userOnBlur() {// Step1()
        if (this.bean.businessType == null || this.bean.businessType == undefined || this.bean.businessType == "") {
            this.businessTypeDispaly = true;
            return
        } else {
            this.businessTypeDispaly = false;
        }
        if (this.bean.userNo == null || this.bean.userNo == undefined || this.bean.userNo == "") {
            this.usernameDispaly = true;
            return
        } else {
            this.usernameDispaly = false;
        }
        // 
        var reg = /^[0-9]*$/;
        if (!reg.test(this.bean.userNo)) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: "" }];
            return
        }
        // 
        if (this.bean.businessType == '01') {
            if (!(this.bean.userNo.length == 15 || (this.bean.userNo.length == 16 && this.bean.userNo.substring(0, 1) == "9"))) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: "" }];
                return
            }
        }
        this.queryUser('blur');
    }

    queryUser(item) {// Step2()
        if (this.tableValueList.length > 0 && this.tableValueList[0].busType != this.bean.businessType) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: "" }];
            return
        }
        let param = {
            busType: this.bean.businessType,
            accountNo: this.bean.userNo,
        };
        this.httpService.queryAccountInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.bean.customerNo = data.custCode;//
                this.bean.customerName = data.custName;//
                this.bean.openInstitution = data.openBanch;//
                this.bean.openInstitutionName = data.openBanchName;//
                this.bean.oldInto = data.oldDepInfo;//
                this.bean.subjectNo = data.glCode;//
                this.bean.bankCode = data.bankCode;//
                if (item == 'save') {
                    setTimeout(() => {
                        this.add();
                    }, 1000)
                }
            } else {
                this.emptyAccount();
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.emptyAccount();
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        });
    }

    add() {// Step3(tableValueListAdd)
        // 
        if (this.bean.customerNo == null || this.bean.customerNo == undefined || this.bean.customerNo == "" ||
            this.bean.openInstitution == null || this.bean.openInstitution == undefined || this.bean.openInstitution == "") {
            this.msgs = [];
            this.msgs = [{ severity: 'erro', summary: '', detail: '' }];
            return
        }
        // 
        if (this.tableValueList.length > 0 && this.tableValueList[0].busType != this.bean.businessType) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: "" }];
            return
        }
        // 
        let bool = true;
        for (var i = 0; i < this.tableValueList.length; i++) {
            if (this.tableValueList[i].accountNo == this.bean.userNo) {
                bool = false;
                break;
            }
        }
        // 
        if (bool) {
            let param = {
                busType: this.bean.businessType,//
                seqno: this.shareholdingNo,//
                accountNo: this.bean.userNo,//
                custCode: this.bean.customerNo,//
            };
            this.httpService.checkAccountInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.tableValueListAdd = [];
                    this.tableValueListAdd.push({
                        "busType": this.bean.businessType,
                        "accountNo": this.bean.userNo,
                        "custCode": this.bean.customerNo,
                        "custName": this.bean.customerName,
                        "openBranch": this.bean.openInstitution,
                        "openBanchName": this.bean.openInstitutionName,
                        "oldDepInfo": this.bean.oldInto,
                        "kmh": this.bean.subjectNo,
                        "bankCode": this.bean.bankCode,
                        "divideLoseDate": this.commfunc.transDateN(this.divideLoseDate),
                    });
                    this.userQuery();
                } else {
                    this.userResetAllInfo();
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                }
            }, err => {
                this.userResetAllInfo();
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            });
        } else {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: "" }];
            this.userResetAllInfo();
        }
    }

    userQuery() {// Step4(tableValueListAdd)
        if (this.tableValueListAdd.length == 0) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        } else {
            this.userResetAllInfo();
            let param = {
                operationType: 0,//
                seqno: this.shareholdingNo,//
                accountList: this.tableValueListAdd//List
            };
            this.httpService.createJointStocksInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    // 
                    this.userResetPage('update');
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                }
            }, err => {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '' }];
            });
        }

    }

    userResetAllInfo() {// Step5()
        this.businessTypeDispaly = false;
        this.usernameDispaly = false;
        this.bean.businessType = undefined;
        this.bean.userNo = undefined;
        this.bean.oldInto = undefined;
        this.bean.customerNo = undefined;
        this.bean.customerName = undefined;
        this.bean.openInstitution = undefined;
        this.bean.openInstitutionName = undefined;
        this.bean.oldInto = undefined;
        this.bean.subjectNo = undefined;
        this.bean.bankCode = undefined;
        this.bean.userNo = undefined;
        this.bean.businessType = undefined;
    }

    preservation() {// Step6()
        // 
        if (this.bean.businessType == null || this.bean.businessType == undefined || this.bean.businessType == "") {
            this.businessTypeDispaly = true;
        } else {
            this.businessTypeDispaly = false;
        }
        if (this.bean.userNo == null || this.bean.userNo == undefined || this.bean.userNo == "") {
            this.usernameDispaly = true;
        } else {
            this.usernameDispaly = false;
        }
        if (this.businessTypeDispaly || this.usernameDispaly) {
            return
        }
        // 
        var reg = /^[0-9]*$/;
        if (!reg.test(this.bean.userNo)) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: "" }];
            return
        }
        // 
        if (this.bean.businessType == '01') {
            if (!(this.bean.userNo.length == 15 || (this.bean.userNo.length == 16 && this.bean.userNo.substring(0, 1) == "9"))) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: "" }];
                return
            }
        }
        // 
        if (this.divideLoseDate == null || this.divideLoseDate == undefined) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: "" }];
            return
        }
        // 
        if (this.bean.customerNo == null || this.bean.customerNo == undefined || this.bean.customerNo == "" ||
            this.bean.openInstitution == null || this.bean.openInstitution == undefined || this.bean.openInstitution == ""
        ) {
            this.queryUser('save');// 
        } else {
            // 
            this.add();
        }
    }

    emptyAccount() {// 
        this.bean.customerNo = undefined;
        this.bean.customerName = undefined;
        this.bean.openInstitution = undefined;
        this.bean.openInstitutionName = undefined;
        this.bean.oldInto = undefined;
    }

    depositDelete(item, ri) {// -

        this.confirmationService.confirm({
            message: '?',
            accept: () => {
                let param = {};
                let arr = [];
                arr.push(item)
                // 
                param = {
                    operationType: 1,//
                    seqno: this.shareholdingNo,//
                    accountList: arr,//List
                };
                this.httpService.createJointStocksInfo(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        this.queryAddUserList('delete');
                    } else {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                });
            }
        });
    }

    // ----------------------
    cooperatorOnBlur() {// Step1()
        // 
        if (this.cooperatorBean.cooperator == null || this.cooperatorBean.cooperator == undefined || this.cooperatorBean.cooperator == "") {
            this.cooperatorDispaly = true;
            return
        } else {
            this.cooperatorDispaly = false;
        }
        // 
        let param = {
            assitId: this.cooperatorBean.cooperator
        };
        this.httpService.queryAssitIdInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.cooperatorBean.cooperator = data.assitId;//id
                this.cooperatorBean.cooperatorName = data.assitName;//
                this.cooperatorBean.institution = data.assitBranch;//
                this.cooperatorBean.institutionName = data.assitBranchName;//
                this.directorLisr = []
                for (var i = 0; i < data.mgrList.length; i++) {
                    this.directorLisr.push({ "label": data.mgrList[i].assitMgrName, "value": data.mgrList[i].assitMgr });//
                }
                if (this.directorLisr.length == 1 && this.directorLisr[0].value == '000') {
                    this.isShowZG = true;
                } else {
                    this.isShowZG = false;
                }
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        });
    }

    cooperatorPreservation() {// Step3()

        // ++
        if (this.cooperatorBean.cooperator == null || this.cooperatorBean.cooperator == undefined || this.cooperatorBean.cooperator == "") {
            this.cooperatorDispaly = true;
        } else {
            this.cooperatorDispaly = false;
        }
        if (this.cooperatorBean.proportion == null || this.cooperatorBean.proportion == undefined || this.cooperatorBean.cooperator == "") {
            this.proportionDispaly = true;
        } else {
            this.proportionDispaly = false;
        }
        if (this.isShowZG) {
            this.cooperatorBean.director = '';
        } else {
            if (this.cooperatorBean.director == null || this.cooperatorBean.director == undefined || this.cooperatorBean.director == "") {
                this.directorDispaly = true;
            } else {
                this.directorDispaly = false;
            }
        }

        // 
        if (this.cooperatorBean.divideLoseFlag == '' || this.cooperatorBean.divideLoseFlag == undefined || this.cooperatorBean.divideLoseFlag == null) {
            this.isLoseEfficacyAssitDispaly = true;
        } else {
            this.isLoseEfficacyAssitDispaly = false;
        }

        if (this.cooperatorDispaly || this.proportionDispaly || this.directorDispaly || this.isLoseEfficacyAssitDispaly) {
            return
        }
        // 
        var regAmt = /^[1-9]{0,1}[0-9]{0,1}(\.[0-9]{0,2})?$/;
        var regAmtMax = /^[1]{1}[0]{1}[0]{1}(\.[0]{0,5})?$/;
        if (!regAmtMax.test(this.cooperatorBean.proportion)) {
            if (!regAmt.test(this.cooperatorBean.proportion)) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: "0100" }];
                return
            } else if (parseFloat(this.cooperatorBean.proportion) == 0) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: "0100" }];
                return
            } else {
                // ..
                if (this.cooperatorBean.proportion.indexOf('.') != -1) {
                    var arr = [];
                    arr = this.cooperatorBean.proportion.split('.');
                    if (arr[0] == '' || arr[1] == '') {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '', detail: "0100" }];
                        return
                    }
                }
            }
        }
        //
        let bool = true;
        let isDivideLoseFlag = false;
        for (var i = 0; i < this.cooperatorTableValueList.length; i++) {
            if (this.cooperatorTableValueList[i].assitId == this.cooperatorBean.cooperator) {
                bool = false;
                break;
            }
            if (this.userAllCard && this.cooperatorBean.divideLoseFlag == '0') {
                if (this.cooperatorTableValueList[i].divideLoseFlag == '0') {
                    isDivideLoseFlag = true;
                    break;
                }
            }
        }
        if (isDivideLoseFlag) {
            setTimeout(() => {
                this.cooperatorBean.divideLoseFlag = '';
            }, 1000)
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: "!" }];
            return
        }
        if (bool) {
            for (var i = 0; i < this.directorLisr.length; i++) {
                if (this.directorLisr[i].value == this.cooperatorBean.director) {
                    this.cooperatorBean.directorName = this.directorLisr[i].label;//
                }
            }
            this.cooperatorTableValueListAdd = [];
            this.cooperatorTableValueListAdd.push({
                "assitId": this.cooperatorBean.cooperator,
                "assitName": this.cooperatorBean.cooperatorName,
                "assitBranch": this.cooperatorBean.institution,
                "assitBranchName": this.cooperatorBean.institutionName,
                "assitMgr": this.cooperatorBean.director,
                "assitMgrName": this.cooperatorBean.directorName,
                "dividerate": this.cooperatorBean.proportion,
                "divideLoseFlag": this.cooperatorBean.divideLoseFlag,
            });
            this.cooperatorQuery();
        } else {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: "" }];
            this.cooperatorReset();
        }
    }

    cooperatorQuery() {// Step3(cooperatorTableValueListAdd)
        if (this.cooperatorTableValueListAdd.length == 0) {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        } else {
            this.cooperatorReset();
            let num = 0;
            this.cooperatorTableValueList.forEach(element => {
                num = num + parseFloat(element.dividerate);
            });
            // +
            num = num + parseFloat(this.cooperatorTableValueListAdd[0].dividerate)
            if (num > 100) {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: '100%' }];
                return
            }
            let param = {};
            // 
            param = {
                operationType: 0,//
                seqno: this.shareholdingNo,//
                assitList: this.cooperatorTableValueListAdd//List
            };

            if (this.userAllCard) { //    

                if (this.tableValueList.length > 0) {// 1
                    this.httpService.createJointStocksInfoByCust(param).subscribe(data => {
                        if (data.returnCode.code == 'success') {
                            // 
                            this.cooperatorResetPage();
                        } else {
                            this.msgs = [];
                            this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                        }
                    }, err => {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                    })
                } else {//2
                    let presUpParam = {
                        seqno: this.shareholdingNo, //
                        optType: '0', //
                        custCode: this.bean.customerNo, //
                        custName: this.bean.customerName, //
                        divideLoseDate: this.commfunc.transDateN(this.divideLoseDate), //
                        openBranch: this.bean.openInstitution, //
                        bankCode: this.bean.bankCode, //
                        assitList: this.cooperatorTableValueListAdd //
                    }
                    this.httpService.createJointStocksInfoByPresup(presUpParam).subscribe(data => {
                        if (data.returnCode.code == 'success') {
                            // 
                            this.cooperatorResetPage();
                        } else {
                            this.msgs = [];
                            this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                        }
                    }, err => {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                    });
                }
            } else { // 
                this.httpService.createJointStocksInfo(param).subscribe(data => {
                    if (data.returnCode.code == 'success') {
                        // 
                        this.cooperatorResetPage();
                    } else {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                    }
                }, err => {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                });
            }
        }
    }

    cooperatorDelete(item, ri) {// -
        this.confirmationService.confirm({
            message: '?',
            accept: () => {
                if (this.tableValueList.length == 0 || this.tableValueList[0].accountNo) {
                    let param = {};
                    let arr = [];
                    arr.push(item)
                    // 
                    param = {
                        operationType: 1,//
                        seqno: this.shareholdingNo,//
                        assitList: arr,//List
                    };
                    this.httpService.createJointStocksInfo(param).subscribe(data => {
                        if (data.returnCode.code == 'success') {
                            this.queryAddCooperatorList();
                        } else {
                            this.msgs = [];
                            this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                        }
                    }, err => {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                    });
                } else {
                    let param = {};
                    let arr = [];
                    arr.push(item)
                    // 
                    param = {
                        optType : '1',//
                        seqno: this.shareholdingNo,//
                        assitList: arr,//List
                    };
                    this.httpService.createJointStocksInfoByPresup(param).subscribe(data => {
                        if (data.returnCode.code == 'success') {
                            // 
                            this.queryAddCooperatorList();
                        } else {
                            this.msgs = [];
                            this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                        }
                    }, err => {
                        this.msgs = [];
                        this.msgs = [{ severity: 'error', summary: '', detail: '' }];
                    });
                }
            }
        });
    }

    cooperatorReset() {// -
        this.cooperatorDispaly = false;
        this.proportionDispaly = false;
        this.directorDispaly = false;
        this.isLoseEfficacyAssitDispaly = false;
        this.isShowZG = false;
        this.cooperatorBean.cooperator = undefined;
        this.cooperatorBean.cooperatorName = undefined;
        this.cooperatorBean.institution = undefined;
        this.cooperatorBean.institutionName = undefined;
        this.cooperatorBean.director = undefined;
        this.cooperatorBean.directorName = undefined;
        this.cooperatorBean.proportion = undefined;
        this.cooperatorBean.divideLoseFlag = undefined;
        this.directorLisr = [];
    }

    changeBusinessType() {// -
        this.bean.customerNo = undefined;
        this.bean.customerName = undefined;
        this.bean.openInstitutionName = undefined;
        this.bean.oldInto = undefined;
    }

    docheck(param) {// -
        this.bean.customerNo = undefined;
        this.bean.customerName = undefined;
        this.bean.openInstitutionName = undefined;
        this.bean.oldInto = undefined;
    }

    userResetPage(type?) {// 
        this.userFirst = 0;
        this.userPageNum = 1;//
        this.userPageSize = 10;
        this.queryAddUserList(type);
    }
    cooperatorResetPage() { // 
        this.cooperatorFirst = 0;
        this.cooperatorPageNum = 1;//
        this.cooperatorPageSize = 20;
        this.queryAddCooperatorList();
    }

    queryAddUserList(type?) { // -

        let param = {};
        param = {
            operationType: 0,//
            seqno: this.shareholdingNo,//
            pageNum: this.userPageNum,
            pageSize: this.userPageSize,
        };
        this.httpService.queryJointStocksInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {

                this.tableValueList = [];
                this.tableValueList = data.accountList == null ? [] : data.accountList;
                this.userTotal = data.totalNum;
                this.institution = data.brid;//
                this.createDate = data.createDate; // 
                if (this.canUp) {

                    this.busType = data.diviType;
                    if (this.busType == '04') {
                        this.userAllCard = true;
                        this.noUpdate = true;
                        this.bean.businessType = this.busType;
                        if (this.tableValueList.length > 0) {
                            this.bean.customerNo = this.tableValueList[0].custCode;
                            this.bean.customerName = this.tableValueList[0].custName;
                            this.divideLoseDate = new Date(this.tableValueList[0].divideLoseDate)
                        }
                    } else {
                        this.userAllCard = false;
                        this.divideLoseDate = new Date();
                        this.userResetAllInfo();
                    }
                    this.stockInfo = {
                        tableValueList: this.tableValueList,
                        cooperatorTableValueList: this.cooperatorTableValueList,
                        preReserveSupervisorList: this.preReserveSupervisorList,
                        seqno: this.shareholdingNo,
                        divideLoseDate: this.commfunc.transDateN(this.divideLoseDate),
                        busType: this.busType,
                    }
                    if (type) {
                        this.preReserveSupervisor();
                    }
                }
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        });
    }
    queryAddCooperatorList() {// -
        let param = {};
        param = {
            operationType: 1,//
            seqno: this.shareholdingNo,//
            pageNum: this.cooperatorPageNum,
            pageSize: this.cooperatorPageSize,
        };
        this.httpService.queryJointStocksInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.cooperatorTableValueList = [];
                this.cooperatorTableValueList = data.assitList == null ? [] : data.assitList;
                this.cooperatorTotal = data.totalNum;
                if (this.canUp) {
                    this.stockInfo = {
                        tableValueList: this.tableValueList,
                        preReserveSupervisorList: this.preReserveSupervisorList,
                        cooperatorTableValueList: this.cooperatorTableValueList,
                        seqno: this.shareholdingNo,
                        divideLoseDate: this.commfunc.transDateN(this.divideLoseDate),
                        busType: this.busType,
                    }
                }
                this.cooperatorReset();
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        });

    }

    cooperatorPaginate(event) {//  
        this.cooperatorPageSize = event.rows * 1;
        this.cooperatorPageNum = event.page + 1;
        this.cooperatorFirst = (this.cooperatorPageNum - 1) * this.cooperatorPageSize;
        this.queryAddCooperatorList();
    }

    userPaginate(event) {// 
        this.userPageSize = event.rows * 1;
        this.userPageNum = event.page + 1;
        this.userFirst = (this.userPageNum - 1) * this.userPageSize;
        this.queryAddUserList();
    }

    myUploader(event) {// 
        this.sumNum = 0;
        // if (event.xhr.response) {
        //     this.uploadedFiles = JSON.parse(event.xhr.response);
        // }
        if (event.xhr.response) {
            let response = JSON.parse(event.xhr.response);
            if (response.hasOwnProperty('errorMsg')) {
                this.msgs = [];
                this.msgs.push({ severity: 'error', summary: '', detail: response.errorMsg });
                return
            }
            this.uploadedFiles = response[0];
        }
        if (this.uploadedFiles) {
            let param = {
                seqno: this.shareholdingNo,
                fileUrl: this.uploadedFiles[0].fileUrl,
                fileName: this.uploadedFiles[0].fileName
            }
            this.httpService.batchAccountStocksInfo(param).subscribe(data => {
                if (data.returnCode.code == 'success') {
                    this.succssNum = data.successTotal;//
                    this.failNum = data.failTotal;//
                    if (this.succssNum != null && this.succssNum != '') {
                        this.sumNum = this.sumNum + parseFloat(this.succssNum)
                    }
                    if (this.failNum != null && this.failNum != '') {
                        this.sumNum = this.sumNum + parseFloat(this.failNum)
                    }
                    //console.log(this.sumNum)
                    this.failInfoList = data.accountFailList;
                    this.msgs = [];
                    this.msgs = [{ severity: 'success', summary: '', detail: data.returnCode.message }];
                    this.queryAddUserList();
                    this.errorPage = true;
                } else {
                    this.msgs = [];
                    this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
                }
            });
        }
    }

    showTime() {
        this.stockInfo = {
            tableValueList: this.tableValueList,
            cooperatorTableValueList: this.cooperatorTableValueList,
            preReserveSupervisorList: this.preReserveSupervisorList,
            seqno: this.shareholdingNo,
            divideLoseDate: this.commfunc.transDateN(this.divideLoseDate),
            busType: this.busType,
        }
    }

    down() {// 
        open(this.DOWNLOAD + "?fileName=" + encodeURI("jointMarketImport.xls") + "&fileUrl=" + "/copdata/moban/");
    }

    closeBack() {//
        this.errorPage = false;
    }

    // 
    preReserveSupervisor() {
        let param = {
            seqno: this.shareholdingNo
        };
        this.httpService.queryOldDivideInto(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                if ((data.assiterList ? data.assiterList : []).length != 0) {
                    this.preReserveSupervisorList = data.assiterList.map((item) => {
                        if (item.assitMgr) {
                            item['changeDisplay'] = false;
                        } else {
                            item['changeDisplay'] = true;
                            item['curPreReserveSupervisorList'] = [{ label: '', value: '' }];
                        }
                        return item
                    });
                } else {
                    this.preReserveSupervisorList = [];
                }
                if (this.canUp) {
                    this.stockInfo = {
                        tableValueList: this.tableValueList,
                        cooperatorTableValueList: this.cooperatorTableValueList,
                        preReserveSupervisorList: this.preReserveSupervisorList,
                        seqno: this.shareholdingNo,
                        divideLoseDate: this.commfunc.transDateN(this.divideLoseDate),
                        busType: this.busType,
                    }
                }   
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        });
    }

    change(col, ri) {
        let param = {
            queryType: '1',
            assitId: col.assiterId
        }
        this.httpService.queryAssitIdInfo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.preReserveSupervisorList[ri]['curPreReserveSupervisorList'] = [{ label: '', value: '' }];
                if ((data.mgrList ? data.mgrList : []).length != 0) {
                    data.mgrList.forEach(element => {
                        let objTemp = {};
                        objTemp['label'] = element.assitMgrName;
                        objTemp['value'] = element.assitMgr;
                        this.preReserveSupervisorList[ri]['curPreReserveSupervisorList'].push(objTemp);
                    });
                }
                if (this.canUp) {
                    this.stockInfo = {
                        tableValueList: this.tableValueList,
                        preReserveSupervisorList: this.preReserveSupervisorList,
                        cooperatorTableValueList: this.cooperatorTableValueList,
                        seqno: this.shareholdingNo,
                        divideLoseDate: this.commfunc.transDateN(this.divideLoseDate),
                        busType: this.busType,
                    }
                }
            } else {
                this.msgs = [];
                this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
            }
        }, err => {
            this.msgs = [];
            this.msgs = [{ severity: 'error', summary: '', detail: '' }];
        });
    }

    permissionCheck(id) {//
        return this.commfunc.permissionCheck(id);
    }
}
